<template>
  <div class="main-container">
    <header class="top-info">
      <span class="top-info-text" v-if="fatherComponent === 'home'">{{week}}</span>
      <span class="top-info-text" v-else>第{{quizNum + 1}}题</span>
    </header>
    <div v-if="fatherComponent ==='home'">
      <div class="home-logo item-container"></div>
      <router-link to="quiz" class="start-button button-style"></router-link>
    </div>
    <div v-else>
      <div class="quiz-logo item-container">
        <div class="quiz-list-container">
          <header class="quiz-title">{{quiz[quizNum].quizTitle}}</header>
          <ul>
            <li v-for="(item, index) in quiz[quizNum].questions" :key="item.quizId" @click="choseQuestion(index, item.questionId)">
              <span class="option-letter" :class="{'been-choosed': choosedNum === index}">{{chooseOptionLetter(index)}}</span>
              <span class="options">{{item.questionContent}}</span>
            </li>
          </ul>
        </div>
      </div>
      <span class="next-button button-style" v-if="quizNum < quiz.length - 1" @click="nextQuestion"></span>
      <span class="submit-button button-style" v-else @click="submit"></span>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'MainComponent',
  props: ['fatherComponent'],
  data() {
    return {
      questionId: null, // 答案id
      choosedNum: null // 选中答案索引
    }
  },
  computed: {
    ...mapState([
      'week', // 第几周
      'quizNum', // 第几题
      'quiz' // 题目详情
    ])
  },
  methods: {
    choseQuestion(index, id) {
      this.choosedNum = index
      this.questionId = id
    },
    chooseOptionLetter(index) {
      switch (index) {
        case 0:
          return 'A'
        case 1:
          return 'B'
        case 2:
          return 'C'
        case 3:
          return 'D'
      }
    },
    nextQuestion() {
      if (this.choosedNum !== null) {
        this.choosedNum = null
        this.$store.dispatch('nextQuiz', this.questionId)
      } else {
        alert('您还没有选择答案哦！')
      }
    },
    submit() {
      if (this.choosedNum !== null) {
        // this.choosedNum = null
        this.$store.dispatch('nextQuiz', this.questionId)
        this.$router.push('score')
      } else {
        alert('您还没有选择答案哦！')
      }
    }
  },
  created() {
    this.$store.dispatch('initializeData')
  }
}
</script>

<style lang="less">
.top-info {
  position: absolute;
  top: 0;
  right: 1rem;
  width: 4rem;
  height: 7.5rem;
  background: url('@/images/WechatIMG2.png') no-repeat center/100% 100%;
  .top-info-text {
    position: absolute;
    top: 5.65rem;
    right: 1.1rem;
    font: normal 600 0.55rem 'Microsoft YaHei';
    color: #3d524d;
  }
}
.item-container {
  position: absolute;
  top: 25vw;
  width: 100vw;
  height: 100vw;
}

.home-logo {
  background: url(@/images/homeBanner.png) no-repeat center/85% 85%;
}

.quiz-logo {
  background: url(@/images/quizBackground.png) no-repeat center/85% 85%;
}

.quiz-list-container {
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 60%;
  height: 60%;
  .quiz-title {
    color: #df27f2;
    font: 500 0.8rem 'Microsoft YaHei';
  }
  span {
    display: inline-block;
    color: #eee;
    font: 500 0.7rem 'Microsoft YaHei';
  }
  .option-letter {
    margin-right: 0.3rem;
    transform: translateY(1px);
    width: 0.75rem;
    height: 0.75rem;
    line-height: 0.75rem;
    border: 1px solid rgb(109, 214, 61);
    border-radius: 50%;
    text-align: center;
  }
}
.next-button {
  background: url(@/images/nextQuestion.png) no-repeat center/30vw;
}

.submit-button {
  background: url(@/images/submit.png) no-repeat center/30vw;
}

.button-style {
  position: absolute;
  top: 115vw;
  width: 100vw;
  height: 20vw;
}

.start-button {
  background: url(@/images/start.png) no-repeat center/30vw;
}

.been-choosed {
  background-color: rgba(204, 205, 255, 0.856);
  color: #333 !important;
}
</style>
